<template>
    <g-body-layout
      :pageIndex="pageNumber"
      :pageSize="pageSize"
      :total="total"
      :filterText.sync="filterText"
      @pageIndexChange="pageIndexChange"
      @pageSizeChange="pageSizeChange"
      @suffixSearchCallback="onFilterReady"
    >
      <!-- 按钮组插槽 -->
      <template #btnGroup>
        <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
        </g-btn-group>
      </template>
      <!-- g-ndo-table插槽 -->
      <template #table>
        <g-ndo-table
          :id="id"
          :handName="'数据采集'"
          :dataList="dataList"
          :isTableLoading="loading"
          :total="total"
          @rowClick="rowClick"
        ></g-ndo-table>
      </template>
      <!-- 详细内容插槽 -->
      <template #content>
        <create-or-edit-data-collection
          :ndoEditParams="ndoEditParams"
          :refreshTag="refreshTag"
          :saveTag="saveTag"
          :resetTag="resetTag"
          @childRefresh="childRefresh($event)"
          @rowClick="rowClick($event)"
          v-if="isShowEdit"
        ></create-or-edit-data-collection>
        <g-empty v-else> </g-empty>
      </template>
    </g-body-layout>
  </template>
  
  <script>
  import CreateOrEditDataCollection from "./create-or-edit-data-collection/create-or-edit-data-collection.vue";
  import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
  import { GNdoPageBase } from "@/utils";
  
  export default {
    name: "DataCollection",
    mixins: [GNdoPageBase],
    components: {
      CreateOrEditDataCollection,
    },
  
    data() {
      return {
        isTableLoading: false,
        filterText: "",
      };
    },
  
    mounted() {},
    created() {
      this.getBtnList();
    },
    methods: {
      getBtnList() {
        // 获取按钮列表
        this.btnList = [
          {
            text: this.AppConst.button.create,
            icon: this.AppConst.icon.create,
            eventName: this.AppConst.button.create,
            display: true,
          },
          {
            text: this.AppConst.button.copy,
            icon: this.AppConst.icon.copy,
            eventName: this.AppConst.button.copy,
            display: !!this.id,
          },
          {
            text: this.AppConst.button.delete,
            icon: this.AppConst.icon.delete,
            eventName: this.AppConst.button.delete,
            display: !!this.id,
          },
          {
            text: this.AppConst.button.refresh,
            icon: this.AppConst.icon.refresh,
            eventName: this.AppConst.button.refresh,
            right: true,
            display: this.isShowEdit,
          },
          {
            text: this.AppConst.button.save,
            icon: this.AppConst.icon.save,
            eventName: this.AppConst.button.save,
            right: true,
            type: "primary",
            display: this.isShowEdit,
          },
        ];
      },
      copy() {
        this.createModal(
          { title: "复制", width: "600px" },
          {
            component: GCopyNdo,
            props: {
              entity: {
                copyValue: this.dataList[0].name,
                value: this.dataList[0].name,
              },
            },
            on: {
              success: (res) => {
                console.log(res);
                // 手动延时
                this.loading = true;
                setTimeout(() => {
                  this.loading = false;
                  this.copySuccess(res.value, res.copyValue);
                  this.refreshGoFirstPage();
                }, 500);
              },
            },
          }
        );
      },
      delete() {
        // 删除确认
        this.$confirm({
          title: "确认删除",
          content: "确认删除该数据采集配置吗？删除后无法恢复。",
          onOk: () => {
            this.loading = true;
            setTimeout(() => {
              this.loading = false;
              // 消息提示
              this.deleteSuccess();
              // 刷新
              this.refresh();
            }, 500);
          },
        });
      },
      fetchData(request) {
        this.loading = true;
        setTimeout(() => {
          request.finishedCallback();
          // 填充dataList 示例数据
          let dataList = [];
          const collectionTypes = [
            "设备数据采集",
            "生产过程采集",
            "质量检测采集",
            "工艺参数采集",
            "环境数据采集"
          ];
          
          const deviceTypes = [
            "传感器",
            "PLC",
            "仪表",
            "摄像头",
            "控制器"
          ];
          
          const protocols = ["Modbus", "OPC UA", "MQTT", "HTTP", "TCP/IP"];
          const statuses = ["已启用", "已禁用", "配置中", "异常"];
          
          for (let i = 0; i < 25; i++) {
            const typeIndex = i % 5;
            const deviceTypeIndex = i % 5;
            const protocolIndex = i % 5;
            const statusIndex = i % 4;
            
            // 生成随机的采集频率
            const frequencies = [1, 5, 10, 30, 60];
            const frequencyIndex = i % 5;
            const frequency = frequencies[frequencyIndex];
            
            // 生成随机的上次采集时间
            const now = new Date();
            const lastCollectTime = new Date(now.getTime() - (i * 10000));
            const lastCollectTimeStr = `${lastCollectTime.getFullYear()}-${(lastCollectTime.getMonth() + 1).toString().padStart(2, '0')}-${lastCollectTime.getDate().toString().padStart(2, '0')} ${lastCollectTime.getHours().toString().padStart(2, '0')}:${lastCollectTime.getMinutes().toString().padStart(2, '0')}:${lastCollectTime.getSeconds().toString().padStart(2, '0')}`;
  
            dataList[i] = {
              id: i + 1,
              collectionCode: `DC${String(i + 1).padStart(4, "0")}`,
              name: `${collectionTypes[typeIndex]}${i % 9 + 1}`,
              collectionType: collectionTypes[typeIndex],
              deviceType: deviceTypes[deviceTypeIndex],
              protocol: protocols[protocolIndex],
              frequency: `${frequency}秒/次`,
              ipAddress: `192.168.${i % 255}.${(i * 2) % 255}`,
              port: 5000 + (i * 10) % 5000,
              dataPoints: 3 + (i % 10),
              lastCollectTime: lastCollectTimeStr,
              description: `${collectionTypes[typeIndex]}，使用${protocols[protocolIndex]}协议，从${deviceTypes[deviceTypeIndex]}采集数据`,
              status: statuses[statusIndex],
              createdBy: "admin",
              createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
              modifiedBy: "admin",
              modifiedTime: "2024-03-" + (20 + i % 8) + " " + (13 + i % 10) + ":" + (i % 60),
            };
          }
  
          // filter 过滤
          if (this.filterText) {
            dataList = dataList.filter((item) => {
              return item.collectionCode.indexOf(this.filterText) > -1 || 
                     item.name.indexOf(this.filterText) > -1 ||
                     item.collectionType.indexOf(this.filterText) > -1 ||
                     item.deviceType.indexOf(this.filterText) > -1 ||
                     item.protocol.indexOf(this.filterText) > -1 ||
                     item.ipAddress.indexOf(this.filterText) > -1 ||
                     item.description.indexOf(this.filterText) > -1;
            });
          }
  
          request.successCallback({
            items: dataList,
            totalCount: dataList.length * 2,
          });
        }, 1000);
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style> 